.scoreboard-form-item, .form-item, .possession-form-item {
    height: auto;
    padding: auto;
    display: inline-block;
    font-size: 2.5vh;
    margin: 1vh;
    border: .1vh solid #54565b;
    border-radius: 1vh;

}

.form-item {
    width: 20%;
}

.scoreboard-form-item {
    width: 15%;
    margin-right: 3vh;
}

.possession-form-item {
    width: 25%;
    margin: 0; 
    padding: 1vh;
    min-width: "200px";
}

.endzone-text-label {
    color: white;
    height: auto;
    display: inline-block;
    vertical-align: middle;
    margin: 1vh;
    margin-right: 2vw;
    border-bottom: .1vh solid #0984e3;
    font-size: 2vh;
}

.endzone-text-input {
    padding: .75vh;
    font-family: "Audiowide";
    color: white;
    background-color: rgba(0, 0, 0, 0);
    border: .1vh solid #0984e3;
    height: auto;
    width: 25%;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    border-radius: 1vh;
}

.endzone-select {
    background-color: rgba(0, 0, 0, 0);
    font-family: "Audiowide";
    border: .1vh solid #0984e3;
    background-color: #0a0d14;
    border-radius: 1vh;
    margin: 0;
    padding: .5vh;
    color: white;
    width: 50%;
    font-size: 1.5vh;
}


@media (max-width: 1200px) {
    .form-item {
        min-width: 200px;
    }

}

@media (max-width: 800px){
    .scoreboard-form-item, .possession-form-item, .form-item {
        width: 90%;
        margin: 1vh;
        padding: 0vh;
    }

    .endzone-text-input {
        min-width: 100px;
        
    }

    .endzone-select {
        min-width: 100px;
    }
}