@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

.help-container-tool-icon {
    position: relative;
    float: left;
    color: white;
    left: .5vw;
    top: 2vh;    
}

.help-container-tool-icon:hover {
    cursor: pointer;
    color: #0984e3;
}

.help-container-tool-expand {
    margin: 1vh;
}


.help-div h3 {
    font-size: 2.5vh;
    font-weight: 100;
    margin: 1vh;
    color: white;
    padding-bottom: 1vh;
    border-bottom: .1vh solid #0984e3;
}

.help-instructions {
    padding-left: 2vw;
    padding-right: 2vw;
    text-align: left;
    width: 80%;
    height: 75%;
    overflow-y: auto;
    color: white;
    font-size: 1.75vh;
}

.help-instructions li {
    margin: 1vh;
}