@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: auto;
    overflow-y: auto;
    background-color: #0d1019;
    font-family: "Audiowide";
    padding-bottom: 10vh;
}

.entry-container {
    width: 100%;
    height: auto;
    display: inline-block;
    text-align: center;
}

.entry-header {
    display: inline-flex;
    justify-content: space-evenly;
    width: 100%;
    height: 7.5vh;
    margin: auto;
    background-color: #23252c;
    border: .1vh solid #9d9ea1;
    border-left: none;
    border-right: none;
    border-top: none;

}

.entry-scoreboard {
    width: 100vw;
    height: auto;
    background-color: #23252c;
    border: .1vh solid #9d9ea1;
    border-left: none;
    border-right: none;
    border-top: none;
    padding: 1vh;
    margin: auto;
    margin-top: 0vh;
    height: auto;
    display: inline-flex;
    justify-content: space-evenly;
}


.offensive-section, .defensive-section {
    position: absolute;
    min-width: 350px;
    z-index: 5;
    width: 20vw;
    height: 60vh;
    background-color: #23252c;
    display: inline-block;
    border: .1vh solid #9d9ea1;

    border-radius: 0;
    min-width: 350px;
}

.offensive-section {
    left: 0;
    border-bottom-right-radius: 1vh;
}

.defensive-section {
    right: 0;
    border-bottom-left-radius: 1vh;
}



.score-div {
    width: 15%;
    height: auto;
    margin: auto;
    font-size: 3vh;
    padding: .5vh;
    border: .1vh dotted #6c6e72;
    border-radius: 1vh;
}

.entry-field {
    width: 58vw;
    height: 55vh;
    background-color: black;
    margin: auto;
    border: .25vh solid #6c6e72;
}

.field-message-div {
    position: relative;
    z-index: 1;
    height: 7vh;
    width: 100%;
    padding: 1vh;
}

.field-message-div p {

    font-size: 2vh;

    text-align: center;
    margin-bottom: 1vh;
}

.field-message-text{
    color: #0984e3;
    font-weight: 100;
}

.field-tracker {
    display: inline-flex;
    justify-content: space-evenly;
    width: 100%;
    height: 5vh;
    background-color: #23252c;
    border-top: .1vh solid #9d9ea1;
    padding: 0vh;
}

.possession-div {
    width: 100%;
    display: inline-flex;
    justify-content: center;
    vertical-align: middle;
    align-items: center;
}

.change-possession {
    position: absolute;
}

.change-possession:hover {
    transform: scale(1.1);
    cursor: pointer;
}

.entry-body {
    margin-top: 3vh;
    width: 100%;
    height: auto;
}

.top-body {
    display: inline-flex;
    justify-content: space-evenly;
    height: auto;
    width: 100%;
    background-color: #23252c;

}


.table-div {
    display: inline-block;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: auto;
    z-index: 5;

}

.table-button-div {
    position: fixed;
    bottom: -1vh;
    display: inline-flex;
    justify-content: center;
    vertical-align :middle;
    left: 48.5%;
    margin: auto;
    z-index: 6;
}

.table-open {
    width: 100%;
    z-index: 6;
}


.table-open:hover {
    cursor: pointer;
    transform: scale(1.1);
}

.table-close {
    display: none;
    z-index: 6;
    margin-bottom: 2vh;
}

.table-close:hover {
    cursor: pointer;
    transform: scale(1.1);
}

.table-body {

    background-color: white;
    padding: 1vh;
    display: none;
    z-index: 2;
    width: 100%;
    height: 25vh;
    min-height: 300px;
}

.table-body .table {
    font-family: "Audiowide";
}

.direction-btn {
    margin-top: 1vh;
}

.direction-btn:hover {
    transform: scale(1.1);
    animation: .5s ease-in-out;
}

.blitz-div {
    display: inline-block;
    position: relative;
    text-align: left;
    padding: 1vh;
    width: 100%;
    height: 100%;
    margin: 0vh;
}

.endzone-blitz-label {
    margin-top: 1vh;
    font-size: 2vh;
    color: white;
    margin-right: .5vw;
    border-bottom: .1vh solid #0984e3;
}

.blitz-input {
    position: relative;
    margin-right: 1vh;
    margin-top: 1vh;
    color: green;
    background-color: teal;
}



@keyframes tool-hover {
    0% {
        border-bottom: .5vh solid #0984e3;

    }

    33% {
        border-right: .5vh solid #0984e3;
    }

    66% {
        border-top: .5vh solid #0984e3;
    }
    
    100% {
        background-color: #0984e3;
        border: none
    }
}


@media (max-width: 800px) {
    .entry-header {
        display: inline-block;
        position: relative;
        height: auto;
        width: 100vw;
    } 

    .entry-header {
        display: inline-block;
        position: relative;
        height: auto;
        width: 100vw;
    } 

    .entry-scoreboard {
        display: inline-block;
        position: relative;
        height: auto;
        width: 100vw;
    } 

    .offensive-section {
        position: relative;
        width: 100%;
    } 
    .defensive-section {
        position: relative;
        width: 100%;
    }

    .blitz-div {
        text-align: center;
    }

    .score-div {
        width: 90%;
    }
}